/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html,
body {
  height: 100%;
  font-family: 'Roboto', sans-serif;
}
.App {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  min-height: 1000px;
}
.App header {
  font-size: 25px;
  padding-top: 40px;
  font-weight: 700;
  width: 100%;
  height: 80px;
  margin: 20px;
  max-width: 500px;
}
.App .Notes-Section {
  -ms-flex-positive: 1;
      flex-grow: 1;
  background-color: #F4F4F4;
}
.App .Notes-Section .Notes-Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  overflow-wrap: break-word;
  margin-bottom: 75px;
}

.Sidebar-container {
  font-weight: 700;
  border: 1px solid grey;
  border-left: none;
  font-size: 45px;
  width: 100%;
  max-width: 275px;
  min-height: 100%;
  margin: 0;
  background-color: #D3D3D3;
}
.Sidebar-container .Sidebar {
  margin: 15px 0 15px 15px;
  width: 275px;
}
.Sidebar-container .Sidebar .Sidebar-buttons {
  margin-top: 15px;
  margin-left: -20px;
  width: 250px;
  font-size: 18px;
}
.Sidebar-container .Sidebar .Sidebar-buttons .Sidebar-button {
  border: 1px solid grey;
  background-color: #00CED1;
  overflow: hidden;
  margin: 15px 0 0 15px;
  width: 250px;
  height: 50px;
  font-weight: 800;
  color: white;
}

.HomePage-container{
  display: -ms-flexbox;
  display: flex;
}
.HomePage-Text {
  font-size: 45px;
  font-weight: 800;
  margin: 55px;
}


